(function() {
  'use strict';

  angular
    .module('unterFrontApp', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .controller('WarehouseController',['$scope','$state', '$http', WarehouseController]);

  /** @ngInject */
  function WarehouseController($scope, $state, $http) {
    $scope.warehouses = {};

    $scope.toGates = function(){
      $state.go('gates', {warehouse : $scope.warehouse, date : $scope.date});
    };

    $http.get("/warehouses").success(function(data){
      $scope.warehouses = data;
    }).error(function (error) {
      console.log(error);
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .controller('GatesController',['$scope','$state', '$stateParams', '$http','$filter', GatesController]);

  /** @ngInject */
  function GatesController($scope, $state, $stateParams, $http, $filter) {
    console.log('/gateslots/reservations?warehouseName='+$state.params.warehouse+'&loadDate='+ $filter('date')($state.params.date,'yyyy-MM-dd'));
    $scope.gateSlotReservations = [];
    $http({
      method: 'GET',
      url: "/gateslots/reservations",
      params : {
        warehouseName:$state.params.warehouse,
        loadDate:$filter('date')($state.params.date,'yyyy-MM-dd')
      }
    })
    .success(function(data){
      $scope.gateSlotReservations = data;
    }).
    error(function(error){
      console.log(error);
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .controller('MainController',['$scope','$state', MainController]);

  /** @ngInject */
  function MainController($scope, $state) {
    $scope.user='ravago';
    $scope.pass='ravago';
    $scope.login = function(){
      if ($scope.user == $scope.username && $scope.pass == $scope.password){
        $state.go('warehouse');
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      }).state('warehouse',{
        url:'/warehouse',
        templateUrl: 'app/main/warehouse.html',
        controller: 'WarehouseController',
        controllerAs:'warehouse'
      }).state('gates',{
        url:'/gates',
        templateUrl: 'app/main/gates.html',
        controller: 'GatesController',
        controllerAs:'gates',
        params:{
          warehouse:null,
          date:null
        }
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('unterFrontApp')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("unterFrontApp").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/gates.html","<div>gates</div>");
$templateCache.put("app/main/main.html","<div class=container><div input-group><span class=input-group-addon id=basic-addon1>User</span> <input type=text ng-model=username class=form-control placeholder=Username aria-describedby=basic-addon1></div><br><div input-group><span class=input-group-addon id=basic-addon1>Password</span> <input type=password ng-model=password class=form-control placeholder=Password aria-describedby=basic-addon1></div><br><input type=button ng-click=login(); value=Login class=form-control></div>");
$templateCache.put("app/main/warehouse.html","<div><div input-group><span class=input-group-addon id=basic-addon1>Warehouse</span><select class=form-control ng-model=warehouse><option ng-repeat=\"wh in warehouses\" value={{wh.name}}>{{wh.name}}</option></select></div><br><div input-group><span class=input-group-addon id=basic-addon1>Date</span> <input type=date ng-model=date class=form-control aria-describedby=basic-addon1></div><br><input type=button ng-click=toGates(); value=\"To Gates\" class=form-control></div>");}]);